<template>
   <div class="text-center">
     <v-btn
       color="primary"
     >
       Open Modal form
 
       <v-dialog
         v-model="dialog"
         activator="parent"
         width="auto"
       >
         <v-card>
           <v-card-text>
            Leave an aplication here!
           </v-card-text>
           <v-card-actions class="d-flex flex-column justify-space-between">
            <label for="forma" class="wr_forma">
               <input v-model="user_name" type="text" name="user_name" placeholder="Name">
               <input v-model="user_secName" type="text" name="user_secName" placeholder="Surname">
               <input @keydown.enter="sendForm(e)" v-model="user_phone" type="number" name="user_phone" placeholder="Phone Number" class="input_num">
               <div class="error_log _unvisible">Error! Fill in all the inputs!</div>
               <button @click.prevent="sendForm(e)" type="submit" class="btn_sub">Submit</button>
             </label>
             <strong class="success_order" v-if="sent_successful">Successful!</strong>
             <v-btn color="primary" block @click="dialog = false">Close Dialog</v-btn>
           </v-card-actions>
         </v-card>
       </v-dialog>
     </v-btn>
   </div>
 </template>
 <script>
   export default {
     data () {
       return {
         dialog: false,

         user_name: "",
         user_secName: "",
         user_phone: null,
         sent_successful: false,
       }
     },
     methods: {
       sendForm(){
         const errorDiv = document.querySelector(".error_log");
         if((this.user_name != "") && (this.user_secName != "") && (this.user_phone != null)){
           this.user_name = "";
           this.user_secName = "";
           this.user_phone = null;
           errorDiv.classList.add("_unvisible");
           this.sent_successful = true;
   
           setTimeout(() => {
             this.sent_successful = false;
           }, 3500);
       } 
       else {
         errorDiv.classList.remove("_unvisible");
         this.sent_successful = false;
       }
       },
     }
   }
 </script>